import { Component, ViewChild, OnInit } from '@angular/core';
import {ModalDirective} from 'ngx-bootstrap/modal';
import { PartParoduct } from '../../_models/PartParoduct';
import { Processor } from '../../_models/Processor';
import { RequirementData } from '../../_models/RequirementData';
import { LoadspecsService } from '../../_services/loadspecs.service';
import { AlertifyService } from '../../_services/alertify.service';
import { RequirementService } from '../../_services/requirement.service';
import { SpectDetail } from '../../_models/SpectDetail';
import { ChatBox } from '../../_models/ChatBox';
import { MyLocation } from '../../_models/MyLocation';
import { PurchaseUser } from '../../_models/PurchaseUser';
import { Options } from 'select2';
import { Generation } from '../../_models/Generation';
import { Subject } from 'rxjs';
import { DataTableDirective } from 'angular-datatables';

@Component({
  selector: 'app-stocks',
  templateUrl: './stocks.component.html',
  styleUrls: ['./stocks.component.css']
})
export class StocksComponent implements OnInit {
  pmodel: any = {};
  model: any = {};
  isEmail: boolean = false;
  editmodel: any = {};
  chatmodel: any = {};
  aprovals: any = {};
  makes: PartParoduct[];
  generations: Generation[];
  models: PartParoduct[];
  hdds: SpectDetail[];
  rams: SpectDetail[];
  processors: Processor[];
  pusers: PurchaseUser[];
  chats: ChatBox[];
  locations: MyLocation[];
  requirements: RequirementData[];
  @ViewChild(DataTableDirective) dtElement: DataTableDirective;
  dtOptions: DataTables.Settings = {
    pagingType: 'full_numbers',
    pageLength: 25,
    retrieve: true,
    order: [[0, 'desc']]
};
  dtInstance: DataTables.Api;
  isDtInitialized: boolean = false;
  dtTrigger: Subject<RequirementData> = new Subject();
  roleid: any =  Number(localStorage.getItem('RoleID'));
  userid: any =  Number(localStorage.getItem('UserId'));
  LoginUser: any =  localStorage.getItem('LoginUser');
  @ViewChild('infoModal') public infoModal: ModalDirective;
  @ViewChild('ApproveModal') public ApproveModal: ModalDirective;
  @ViewChild('shareModal') public shareModal: ModalDirective;
  @ViewChild('editModal') public editModal: ModalDirective;
  @ViewChild('ChatModal') public ChatModal: ModalDirective;
  public options: Options;

  constructor(private loadspecsService: LoadspecsService, private alertify: AlertifyService,
    private requirementservice: RequirementService) { }

  ngOnInit(): void {
    this.options = {
      multiple: true,
      theme: 'classic',
      closeOnSelect: false,
      width: '348'
    };
    this.loadMakes();
    this.loadGenerations();
    this.loadProcessors();
    this.loadLocations();
    this.loadRAMs();
    this.loadHDDs();
    this.loadRequirements();
  }
  loadLocations() {
    return this.loadspecsService.loadLocations().subscribe((locations: MyLocation[]) => {
      this.locations = locations.filter(proj => (proj.status === 1));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadMakes() {
    return this.loadspecsService.getMakes().subscribe((partproducts: PartParoduct[]) => {
      this.makes = partproducts.filter(proj => (proj.type === 1));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadGenerations() {
    return this.loadspecsService.getGenerations().subscribe((generations: Generation[]) => {
       this.generations = generations;
    }, error => {
      this.alertify.error(error);
    });
  }
  loadModels(value: number) {
    return this.loadspecsService.getModels().subscribe((partproducts: PartParoduct[]) => {
      this.models = partproducts.filter(proj => (Number(proj.parentId) === Number(value)));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadProcessors() {
    return this.loadspecsService.getProcessors().subscribe((processors: Processor[]) => {
      this.processors = processors;
    }, error => {
      this.alertify.error(error);
    });
  }

  loadRAMs() {
    return this.loadspecsService.loadSpectDetails().subscribe((rams: SpectDetail[]) => {
      this.rams = rams.filter(proj => (proj.parentId === 1));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadHDDs() {
    return this.loadspecsService.loadSpectDetails().subscribe((hdds: SpectDetail[]) => {
      this.hdds = hdds.filter(proj => (proj.parentId === 2));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadRequirements() {
    return this.requirementservice.getRequirements().subscribe((requirements: RequirementData[]) => {
      if (Number(localStorage.getItem('RoleID')) === 4) {
        this.requirements = requirements.filter(proj => (Number(proj.type) === 1  && Number(proj.flag) === 0
        &&  Number(proj.status) !== 2  && Number(proj.userId) === Number(localStorage.getItem('UserId'))));
      } else {
        this.requirements = requirements.filter(proj => (Number(proj.type) === 1  && Number(proj.status) !== 2
        && Number(proj.flag) === 0));
      }
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger.next();
      }
      console.log(this.requirements);
    }, error => {
      this.alertify.error(error);
    });

  }
  sendRequirementPost() {
    this.model.status = 0;
    this.model.Type = 1;
    this.model['userId'] = localStorage.getItem('UserId');
    if (this.model.MakeId === '0') {
      this.alertify.error('Select Make');
      return;
    }
    if (this.model.ModelId === '0') {
      this.alertify.error('Select Model');
      return;
    }
    if (this.model.ProcessorId === '0') {
      this.alertify.error('Select Processor');
      return;
    }
    if (this.model.Ram === null) {
      this.alertify.error('Select RAM');
      return;
    }
    if (this.model.Hdd === null) {
      this.alertify.error('Select HDD');
      return;
    }
    if (this.model.gen_id === null) {
      this.alertify.error('Select Generation');
      return;
    }
    if (!this.model.Qty) {
      this.alertify.error('Enter Qty');
      return;
    }
    if (!this.model.Cname) {
      this.alertify.error('Enter Supplier Name');
      return;
    }
    if (!this.model.Price) {
      this.alertify.error('Enter Price');
      return;
    }
    if (!this.model.Comment) {
      this.alertify.error('Enter Comment');
      return;
    }
    if (!this.model.Adapter) {
      this.alertify.error('Select Adapter');
      return;
    }
    document.getElementById('postingrequest').innerHTML = '<i class="fa fa-circle-o-notch mt-1 fa-lg fa-spin"></i> Sending...';
    (document.getElementById('postingrequest') as HTMLButtonElement).disabled = true;
    this.requirementservice.sendRequirementPost(this.model).subscribe((requirements: RequirementData[]) => {
      if (Number(localStorage.getItem('RoleID')) === 4) {
        this.requirements = requirements.filter(proj => (Number(proj.type) === 1  && Number(proj.flag) === 0
        && Number(proj.status) !== 2  && Number(proj.userId) === Number(localStorage.getItem('UserId'))));
      } else {
        this.requirements = requirements.filter(proj => (Number(proj.type) === 1  && Number(proj.status) !== 2  &&
        Number(proj.flag) === 0));
      }
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger.next();
      }
      this.model.MakeId = '0';
      this.model.ModelId = '0';
      this.model.ProcessorId = '0';
      this.model.Ram = '0';
      this.model.Hdd = '0';
      this.model.req_type = '0';
      this.model.Qty = '';
      this.model.Cname = '';
      document.getElementById('postingrequest').innerHTML = 'Submit';
      (document.getElementById('postingrequest') as HTMLButtonElement).disabled = false;
      this.infoModal.hide();
      this.alertify.success('Deal Added Successfull');
    }, error => {
      this.alertify.error(error);
    });
  }
  openChatModal(id: number) {
    this.model.Ref_Id = id;
    this.chatmodel.ref_id = id;
    this.chatmodel.user_id = this.userid;
    return this.requirementservice.viewChats(this.chatmodel).subscribe((chats: ChatBox[]) => {
      this.chats = chats.filter(proj => (Number(proj.status) === 0 && proj.ref_Id === id));
      this.loadRequirements();
      this.ChatModal.show();
  }, error => {
    this.alertify.error(error);
  });
  }
  sendMessagePost() {
    this.model.U_Id = Number(localStorage.getItem('UserId'));
    if (!this.model.Text) {
      this.alertify.error('Enter Comment');
      return;
    }
    document.getElementById('SendSMS').innerHTML = '<i class="fa fa-circle-o-notch mt-1 fa-lg fa-spin"></i> Sending...';
    (document.getElementById('SendSMS') as HTMLButtonElement).disabled = true;
    this.requirementservice.sendChatPost(this.model).subscribe((chats: ChatBox[]) => {
      this.chats = chats.filter(proj => (Number(proj.status) === 0 && proj.ref_Id === this.model.Ref_Id));
      this.loadRequirements();
      this.model.Text = '';
      document.getElementById('SendSMS').innerHTML = 'Send';
      (document.getElementById('SendSMS') as HTMLButtonElement).disabled = false;
    }, error => {
      this.alertify.error(error);
    });
  }
  EditRequirement(id: any, qty: number, price: any) {
    this.editmodel.requestID = id;
    this.editmodel.tqty = qty;
    this.editmodel.tprice = price;
    this.editModal.show();
  }
  EditReqDataPost() {
    if (!this.editmodel.Qty) {
      this.alertify.error('Enter Qty');
      return;
    }
    if (!this.editmodel.Price) {
      this.alertify.error('Enter Price');
      return;
    }
    console.log(this.editmodel);
    document.getElementById('EditStockData').innerHTML = '<i class="fa fa-circle-o-notch mt-1 fa-lg fa-spin"></i> Sending...';
   (document.getElementById('EditStockData') as HTMLButtonElement).disabled = true;
    this.requirementservice.EditReqDataPost(this.editmodel).subscribe((requirements: RequirementData[]) => {
      if (Number(localStorage.getItem('RoleID')) === 4) {
        this.requirements = requirements.filter(proj => (Number(proj.type) === 1  && Number(proj.flag) === 0
        && Number(proj.status) !== 2  && Number(proj.userId) === Number(localStorage.getItem('UserId'))));
      } else {
        this.requirements = requirements.filter(proj => (Number(proj.type) === 1  && Number(proj.status) !== 2  &&
        Number(proj.flag) === 0));
      }
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger.next();
      }
      this.editmodel.Qty = '';
      this.editmodel.Price = '';
      this.editmodel.Comment = '';
      this.editModal.hide();
      document.getElementById('EditStockData').innerHTML = 'Submit';
     (document.getElementById('EditStockData') as HTMLButtonElement).disabled = false;
      this.alertify.success('Stock Edited Successfuly');
    }, error => {
      this.alertify.error(error);
    });
  }
  openApproveModal(id: any, qty: number) {
    this.model.requestID = id;
    this.model.tqty = qty;
    this.ApproveModal.show();
  }

  ApproveDeals() {
    this.aprovals.app_by  = Number(localStorage.getItem('UserId'));
    if (!this.model.Qty) {
      this.alertify.error('Enter Qty');
      return;
    }
    if (this.model.tqty < this.model.Qty) {
      this.alertify.error('Check Qty');
      return;
    }
    if (!this.model.Comment) {
      this.alertify.error('Enter Comment');
      return;
    }
    this.aprovals.Comment = this.model.Comment;
    this.aprovals.Qty = this.model.Qty;
    this.aprovals.requestID = this.model.requestID;
    document.getElementById('ApproveMydeal').innerHTML = '<i class="fa fa-circle-o-notch mt-1 fa-lg fa-spin"></i> Sending...';
   (document.getElementById('ApproveMydeal') as HTMLButtonElement).disabled = true;
    this.requirementservice.ApproveDeals(this.aprovals).subscribe((requirements: RequirementData[]) => {
      if (Number(localStorage.getItem('RoleID')) === 4) {
        this.requirements = requirements.filter(proj => (Number(proj.type) === 1  && Number(proj.flag) === 0
        && Number(proj.status) !== 2  && Number(proj.userId) === Number(localStorage.getItem('UserId'))));
      } else {
        this.requirements = requirements.filter(proj => (Number(proj.type) === 1  && Number(proj.status) !== 2  &&
        Number(proj.flag) === 0));
      }
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger.next();
      }
      this.model.Qty = '';
      this.model.Comment = '';
      this.ApproveModal.hide();
      document.getElementById('ApproveMydeal').innerHTML = 'Submit';
     (document.getElementById('ApproveMydeal') as HTMLButtonElement).disabled = false;
      this.alertify.success('Deal Approved Successfuly');
    }, error => {
      this.alertify.error(error);
    });
  }
  CloseRequirement(Id: any) {
    this.model = {RequestID : Id, Status: 1};
    this.requirementservice.CloseRequirement(this.model).subscribe(() => {
      document.getElementById('Req_' + Id).innerHTML = '<span style="font-weight:bold;color:green;font-style:italic;font-size:15px">Closed</span>';
      this.alertify.success('Stock Closed');
    }, error => {
      this.alertify.error(error);
    });
  }
  loadPurchaseUser(value: number) {
    return this.loadspecsService.getMyPurchaseUsers().subscribe((pusers: PurchaseUser[]) => {
      this.pusers = pusers.filter(proj => (Number(proj.type) === Number(value)));
    }, error => {
      this.alertify.error(error);
    });

  }
  OpenshareModal(id) {
    this.pmodel.ReqNo1 = id;
    this.shareModal.show();
    this.isEmail = false;
  }

  ShareReqPost() {
    this.pmodel.s_flag = this.isEmail;
    this.pmodel.emp_id = this.userid;
    if (!this.pmodel.s_qty) {
      this.alertify.error('Enter Qty');
      return;
    }
    if (!this.pmodel.r_cur) {
      this.alertify.error('Enter Currency');
      return;
    }
    if (!this.pmodel.s_price) {
      this.alertify.error('Enter Price');
      return;
    }
    if (!this.pmodel.options && this.isEmail) {
      this.alertify.error('Select Email');
      return;
    }
    console.log(this.pmodel);
   document.getElementById('ShareReq').innerHTML = '<i class="fa fa-circle-o-notch mt-1 fa-lg fa-spin"></i> Sending...';
   (document.getElementById('ShareReq') as HTMLButtonElement).disabled = true;
    this.requirementservice.ShareReqPost(this.pmodel).subscribe(() => {
      this.pmodel.options = null;
      this.shareModal.hide();
      this.loadRequirements();
      document.getElementById('ShareReq').innerHTML = 'Send';
     (document.getElementById('ShareReq') as HTMLButtonElement).disabled = false;
      this.alertify.success('Deal has been Shared Successfull');
    }, error => {
      this.alertify.error(error);
    });
  }
  RejectStock(requestID) {
    this.model = {RequestID : requestID, Status: 2, UserId: Number(localStorage.getItem('UserId'))};
    document.getElementById('Reject_' + requestID).innerHTML = '<i class="fa fa-circle-o-notch mt-1 fa-lg fa-spin"></i> Sending...';
    (document.getElementById('Reject_' + requestID) as HTMLButtonElement).disabled = true;
    this.requirementservice.RejectStock(this.model).subscribe(() => {
      this.loadRequirements();
      document.getElementById('Reject_' + requestID).innerHTML = 'Reject';
      (document.getElementById('Reject_' + requestID) as HTMLButtonElement).disabled = false;
      this.alertify.success('Deal has been Shared Successfull');
    }, error => {
      this.alertify.error(error);
    });

  }
  allNonTrades(event) {
    const checked = event.target.checked;
    this.isEmail = checked;
  }


}
